import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { Platform } from 'ionic-angular';
import { SQLite, SQLiteObject } from '@ionic-native/sqlite';
import { SQLitePorter } from '@ionic-native/sqlite-porter';
import { BehaviorSubject } from 'rxjs/Rx';
import { Storage } from '@ionic/storage';


@Injectable()
export class DatabaseProvider {

  database: SQLiteObject;
  private databaseReady: BehaviorSubject<boolean>;

  constructor(public sqlitePorter: SQLitePorter, 
              private storage: Storage, 
              private sqlite: SQLite, 
              private platform: Platform, 
              private http: Http) {
    this.databaseReady = new BehaviorSubject(false);
    this.platform.ready().then(() => {
      this.sqlite.create({
        name: 'SQLiteData.db',
        location: 'default'
      })
        .then((db: SQLiteObject) => {
          this.database = db;
          this.storage.get('database_filled').then(val => {
            if (val) {
              this.databaseReady.next(true);
            } else {
              this.fillDatabase();
            }
          });
        });
    });
  }

  fillDatabase() {
    this.http.get('assets/SQLiteDatos.sql')
      .map(res => res.text())
      .subscribe(sql => {
        this.sqlitePorter.importSqlToDb(this.database, sql)
          .then(data => {
            this.databaseReady.next(true);
            this.storage.set('database_filled', true);
          })
          .catch(e => console.error(e));
      });
  }
 
  setServicios(servicios) {
    var data;
    var se = JSON.parse(servicios);
    var largo = Object.keys(se).length;
    for(var i=0;i<largo;i++){
      let servicio = [se[i].idservicio, 
                      se[i].nombreservicio, 
                      se[i].iconoservicio]
      data = this.database.executeSql("INSERT INTO servicios (idservicio, nombreservicio,"
                                                            +" iconoservicio)"
                                                            +" VALUES (?, ?, ?)", servicio)
    }
    data.then(data => {
      return data;
    }, err => {
      console.log('Error: ', err);
      return err;
    });
  }

  setValoraciones(valoraciones) {
    var data;
    var va = JSON.parse(valoraciones);
    var largo = Object.keys(va).length;
    for(var i=0;i<largo;i++){
      let valoracion = [va[i].idvaloracion, 
                        va[i].nombrevaloracion, 
                        va[i].descripcion, 
                        va[i].foto,
                        va[i].email,
                        va[i].servicio]
      data = this.database.executeSql("INSERT INTO valoraciones (idvaloracion, nombrevaloracion,"
                                                           +" descripcion, foto, email, servicio)"
                                                           +" VALUES (?, ?, ?, ?, ?, ?)", valoracion)
    }
    data.then(data => {
      return data;
    }, err => {
      console.log('Error: ', err);
      return err;
    });
  }

  setUbicaciones(ubicaciones) {
    var data;
    var ub = JSON.parse(ubicaciones);
    var largo = Object.keys(ub).length;
    for(var i=0;i<largo;i++){
      let ubicacion = [ub[i].idubicacion, 
                        ub[i].codigoqr, 
                        ub[i].nombreubicacion, 
                        ub[i].ubicacion]
      data = this.database.executeSql("INSERT INTO ubicaciones (idubicacion, codigoqr,"
                                                           +" nombreubicacion, ubicacion)"
                                                           +" VALUES (?, ?, ?, ?)", ubicacion)
    }
    data.then(data => {
      return data;
    }, err => {
      console.log('Error: ', err);
      return err;
    });
  }

  setUbicacionValoracion(ubicacionesValoraciones) {
    var data;
    var ubva = JSON.parse(ubicacionesValoraciones);
    var largo = Object.keys(ubva).length;
    for(var i=0;i<largo;i++){
      let ubicacionValoracion = [ubva[i].idubicacion_valoracion, 
                                 ubva[i].ubicacion, 
                                 ubva[i].valoracion]
      data = this.database.executeSql("INSERT INTO ubicacion_valoracion (idubicacion_valoracion, "
                                                           +"ubicacion, valoracion)"
                                                           +" VALUES (?, ?, ?)", ubicacionValoracion)
    }
    data.then(data => {
      return data;
    }, err => {
      console.log('Error: ', err);
      return err;
    });
  }

  setLog(log) {
      return this.database.executeSql("INSERT INTO log (idlog) VALUES (?)", log.idlog).then(data => {
      return data;
    }, err => {
      console.log('Error: ', err);
      return err;
    });
  }

  getServicios() {
    return this.database.executeSql("SELECT * FROM servicios", []).then((data) => {
      let datos = [];
      if (data.rows.length > 0) {
        for (var i = 0; i < data.rows.length; i++) {
          datos.push({ idservicio: data.rows.item(i).idservicio, 
                       nombreservicio: data.rows.item(i).nombreservicio, 
                       iconoservicio: data.rows.item(i).iconoservicio });
        }
      }
      return datos;
    }, err => {
      console.log('Error: ', err);
      return [];
    });
  }
 
  getValoraciones(servicio){
    return this.database.executeSql("SELECT * FROM valoraciones v "
                                  +"JOIN servicios s ON v.servicio=s.idservicio "
                                  +"WHERE idservicio = "+ servicio, []).then((data) => {
      let datos = [];
      if (data.rows.length > 0) {
        for (var i = 0; i < data.rows.length; i++) {
          datos.push({ idservicio: data.rows.item(i).idservicio, 
                       nombreservicio: data.rows.item(i).nombreservicio, 
                       iconoservicio: data.rows.item(i).iconoservicio, 
                       nombrevaloracion: data.rows.item(i).nombrevaloracion });
        }
      }
      return datos;
    }, err => {
      console.log('Error: ', err);
      return [];
    });
  }

  getDatabaseState() {
    return this.databaseReady.asObservable();
  }
 

}



